import React, { useEffect, useState } from "react";
import "./RowItem.css";
import {
  PlayArrow,
  Add,
  ThumbUpAltOutlined,
  ThumbDownAltOutlined,
} from "@mui/icons-material";

const base_url = "https://image.tmdb.org/t/p/original/";

function RowItem({ index, title, movie, windowSize, media }) {
  const [isHovered, setIsHovered] = useState(false);
  const poster_width = (120 * 16) / 9 + 5;
  const [runTime, setRunTime] = useState(null);
  const [contentRating, setContentRating] = useState(null);
  const [year, setYear] = useState(null);

  useEffect(() => {
    if (
      media === "TV" ||
      movie.media_type === "tv" ||
      movie.number_of_seasons ||
      movie.number_of_episodes
    ) {
      setRunTime(
        movie?.number_of_seasons +
          " season" +
          (movie?.number_of_seasons !== 1 ? "s" : "") ||
          movie?.number_of_episodes + " episodes" ||
          "Rating: " + movie?.vote_average
      );
      if (movie.content_ratings) {
        setContentRating(movie.content_ratings.results["0"].rating);
      }
      if (movie.first_air_date) {
        setYear(movie.first_air_date.substring(0, 4));
      }
    } else if (
      media === "Movie" ||
      movie.media_type === "movie" ||
      movie.runtime
    ) {
      setRunTime(timeConvert(movie.runtime));
      if (movie.release_dates) {
        movie.release_dates.results.forEach((result) => {
          if (result.iso_3166_1 === "US") {
            setContentRating(result.release_dates["0"].certification);
          }
        });
      }
      if (movie.release_date) {
        setYear(movie.release_date.substring(0, 4));
      }
    } else {
      console.log("Could not find media type");
      console.log(movie);
      console.log(media + " - " + (movie?.title || movie?.name));
    }

    return;
  }, []);

  console.log(movie);

  function timeConvert(time) {
    let minutes = time;
    let m = minutes % 60;
    let h = (minutes - m) / 60;
    return (
      (h !== 0 ? h.toString() + " hour" : "") +
      (h > 0 ? (h !== 1 ? "s " : " ") : "") +
      m.toString() +
      " min" +
      (m !== 1 ? "s" : "")
    );
  }

  function truncate(str, n) {
    return str?.length > n ? str.substr(0, n - 1) + "..." : str;
  }

  return (
    <div
      key={`${title}-${index}-${movie.id}`}
      className="row_poster"
      style={{
        left:
          isHovered &&
          index * poster_width -
            (325 - poster_width) / 2 +
            windowSize.innerWidth * 0.04,
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <img
        className="row_poster_image"
        key={movie.id}
        src={`${base_url}${movie.backdrop_path}`}
        alt={movie.name}
      />
      {movie.images.logos.length !== 0 && (
        <img
          key={movie.id * movie.id}
          className="row_poster_logo"
          src={`${base_url}${movie.images.logos["0"].file_path}`}
          alt={movie.name}
        />
      )}
      {isHovered && (
        <>
          {movie.videos.results.length !== 0 && (
            <iframe
              src={`https://www.youtube.com/embed/${movie.videos.results["0"].key}?autoplay=1&mute=1&controls=0&loop=1`}
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            ></iframe>
          )}
          <div className="row_poster_info">
            <div className="row_poster_icons">
              <PlayArrow className="row_posters_icon" />
              <Add className="row_posters_icon" />
              <ThumbUpAltOutlined className="row_posters_icon" />
              <ThumbDownAltOutlined className="row_posters_icon" />
            </div>
            <div className="row_poster_info_top">
              {runTime && <span>{runTime}</span>}
              {contentRating && <span className="limit">{contentRating}</span>}
              {year && <span className="year">{year}</span>}
            </div>
            <div className="row_poster_desc">
              {truncate(movie?.overview, 99)}
            </div>
            <div className="row_poster_genre">{movie?.genres["0"].name}</div>
          </div>
        </>
      )}
    </div>
  );
}

export default RowItem;
